*{
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  margin: 0;
  padding: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* nav bar */

.nav{
  background-color: transparent;
  height: 120px;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content:right;
  padding: 24px;
/*   box-shadow: 0px 2px 16px #000000cc; */
}

.nav--links{
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: flex-end;
}

.nav--link{
  color: #666666;
  font-size: 14px;
  padding: 18px;
  margin: 2px;
  border: 2px solid transparent;
  display: inline-block;
  text-decoration: none;
  transition: .3s;
}

.nav--link:hover{
  cursor: pointer;
  color: #EAA01F;
  border-bottom: 2px solid #EAA01F;
}

.nav--link:focus{
  color: #EAA01F;
}

.container{
  scroll-snap-type: y mandatory /* proximity */;
  overflow-y: scroll;
  height: 100vh;
}

.bod{
  height: 100vh;
  padding: 120px 25px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  scroll-snap-align: start;
}

.bod1{
  background-color: #fafafa;
  color: #212121;
}

.bod2{
  background-color: #212121;
  color: #fafafa;
}

.contact--footer,
#contact{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

/* icons */

.contact--icons>ul{
  display: flex;
  list-style-type: none;
  align-items:baseline;
  justify-content:center;
}

/* coloring and animatingicons on hover */

.icons{
  height: 36px;
  filter: invert(36%);
  margin: 10px;
  transition: 0.3s;
}

.icons:hover{/* 
  margin-top: 2px; */
  padding: 2px;
  filter: invert(100%);
}

.to-top{
  height: 64px;
  position: sticky;
  bottom: 20px;
  right: 0px;
  transition: .3s;
}

.to-top:hover{
  cursor: pointer;
  filter: brightness(0%) invert(100%);
  padding: 4px;
}

/* contact logo */
.contact--logo{
  height: 300px;
  margin: 50px ;
}

/* email */
.contact--email{
  margin: 20px;
}
.email{
  color: #EAA01F;
  font-size: 16px;
  padding: 4px;
  border: 1px solid transparent;
  text-decoration: none;
  transition: 0.3s;
}

.email:hover{
  cursor: pointer;
  border-bottom: 1px solid #EAA01F;
}

/* copyright */
.copyright{
  color: #666666;
  font-size: 12px;
  text-transform: uppercase;
}


/* buttons */
.button{
  color: #EAA01F;
  font-size: 14px;
  font-weight: 500;
  min-width: 100px;
  background: none;
  border: 1px solid #EAA01F;
  padding: 8px 16px;
  margin: 0 20px 0;
  border-radius: 4px;
  transition: 0.3s;
}

.button:hover{
  cursor: pointer;
  background-color: #EAA01F;
  color: #212121;
}